.dialogs {
    width: 100%;
    padding: .5rem;
}

.dialogs-list {
    display: flex;
    flex-direction: column;
    max-width: 200px;
    width: 100%;
    padding-right: .5rem;
    border-right: 2px solid lightblue;
}

.dialogs-list__item {
    width: 100%;
    padding: .5rem;
    margin: 3px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.dialogs-list__item.active {
    border-color: lightcoral;
}

.msg {
    width: 100%;
    padding-left: .5rem;
}